const Legislation = () => (
    <Wrapper title={''}>
        <ul className={classes.list}>
            <li>
                <Link className={classes.link} to={'/legislation/appeal-procedure'}> </Link>
            </li>
            <li>
                <Link className={classes.link} to={'/legislation/legislative-changes'}>      ( 01.01.2019.)</Link>
            </li>
        </ul>
    </Wrapper>
);
export default Legislation;