import classes from './Breadcrumbs.module.scss';
import useBreadcrumbs from "use-react-router-breadcrumbs";
import { NavLink } from "react-router-dom";

const routes = [
    { path: '/', breadcrumb: '' },
    { path: 'news', breadcrumb: '' },
    { path: 'about', breadcrumb: ' ' },
    { path: 'legislation', breadcrumb: '' },
    { path: 'legislation/appeal-procedure', breadcrumb: ' ' },
    { path: 'legislation/legislative-changes', breadcrumb: ' '},
    { path: 'questionnaires', breadcrumb: '' },
    { path: 'temporary-employment', breadcrumb: ' ' },
    { path: 'for-employers', breadcrumb: '' },
    { path: 'feed', breadcrumb: '' }
];

const MapBreadcrumbs = (props) => {
    const breadcrumbs = useBreadcrumbs(routes);

    return breadcrumbs.map(({ match, breadcrumb }, index) => {
        return index === breadcrumbs.length - 1 ? (
            <span key={match.pathname} className={classes.current}>{breadcrumb}</span>
        ) : (
            <div className={classes.wrapper} key={match.pathname}>
                <NavLink
                    className={classes.past}
                    key={match.pathname}
                    to={match.pathname}
                >
                    {breadcrumb}
                </NavLink>
                {props.separator}
            </div>
        );
    });
};

const Breadcrumbs = props => (
    <aside className={classes.breadcrumbs}>
        <MapBreadcrumbs separator={props.separator} />
    </aside>
);
export default Breadcrumbs;